import { useEffect } from "react"
import { ContentPackage } from "@rp/react/tanstack-api/content"

type UseInitializeAllContentPackageProps = {
  currentPackage: ContentPackage | null
  setCurrentPackage: (contentPackage: ContentPackage) => void
  allContentPackages: Array<ContentPackage>
}

function useInitializeAllContentPackage({
  currentPackage,
  setCurrentPackage,
  allContentPackages,
}: UseInitializeAllContentPackageProps): void {
  useEffect(() => {
    if (currentPackage) return

    const allPackage = allContentPackages.find(contentPackage => contentPackage.id === "all")

    if (!allPackage) return

    setCurrentPackage(allPackage)
  }, [allContentPackages, currentPackage, setCurrentPackage])
}

export { useInitializeAllContentPackage }
